import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TambahkanDataPage } from '../pages/tambahkan-data/tambahkan-data';
import { DaftarLayerPage } from '../pages/daftar-layer/daftar-layer';
import { LegendaPage } from '../pages/legenda/legenda';
import { GaleriPetaDasarPage } from '../pages/galeri-peta-dasar/galeri-peta-dasar';
import { PanduanPenggunaanPage } from '../pages/panduan-penggunaan/panduan-penggunaan';

import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireStorageModule } from 'angularfire2/storage';
// import { AngularFireAuthModule } from 'angularfire2/auth';
import { environment } from '../environments/environment';

import { HttpClientModule } from '@angular/common/http';
import { GlobalProvider } from '../providers/global/global';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TambahkanDataPage,
    DaftarLayerPage,
    LegendaPage,
    GaleriPetaDasarPage,
    PanduanPenggunaanPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    AngularFireStorageModule,
    AngularFirestoreModule,
    AngularFireModule.initializeApp(environment.firebase),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TambahkanDataPage,
    DaftarLayerPage,
    LegendaPage,
    GaleriPetaDasarPage,
    PanduanPenggunaanPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    GlobalProvider
  ]
})
export class AppModule {}



