import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalProvider } from "../../providers/global/global";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser'


/**
 * Generated class for the LegendaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-legenda',
  templateUrl: 'legenda.html',
})
export class LegendaPage {

  legend : any;

  constructor(private sanitizer: DomSanitizer, public navCtrl: NavController, public navParams: NavParams, public global: GlobalProvider, public httpClient: HttpClient) {
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LegendaPage');
  }

  ionViewWillEnter() {
    console.log('ionViewWillEnter LegendaPage');
    let headers = new HttpHeaders();
    headers.append('Access-Control-Allow-Origin' , '*');
    headers.append('Access-Control-Allow-Methods', 'POST, GET, OPTIONS, PUT');
    headers.append('Accept','application/json');
    headers.append('content-type','application/json');
    this.httpClient.get(this.global.pilihan + '/legend?f=pjson', {headers}).subscribe((data: any) => {
      if (data.layers) {
        console.log('lalala legend', data.layers);
        this.legend = data.layers;
      }
    }); 
  }

  getImgContent(n): SafeUrl {
    return this.sanitizer.bypassSecurityTrustUrl(n);
}


}



